import axios from 'axios';
// const baseURL = `${process.env.REACT_APP_WORKOUT_API}/api`;
const baseURL = `${process.env.REACT_APP_API_URL}`;
const prodURL = `${process.env.REACT_APP_API_URL}`;

// USER ROUTES ---------------------------------------------

export const signup = (user) => {
    return axios.post(`${prodURL}/signup`, user, { withCredentials: true });
}

export const updateUser = (user) => {
    return axios.post(`${prodURL}/updateuser`, user, { withCredentials: true });
}

export const login = (user) => {
    return axios.post(`${prodURL}/login`, user, { withCredentials: true });
}

export const verify = (storedToken) => {
    return axios.get(`${prodURL}/verify`, { withCredentials: true, headers: { Authorization: `Bearer ${storedToken}` }
    });
}

// USER WORKOUT ROUTES ------------------------------------------

export const getAllWorkouts = () => {
    return axios.get(`${prodURL}/workout`, { withCredentials: true });
}

export const getWorkout = (id) => {
    return axios.get(`${prodURL}/workout/${id}`, { withCredentials: true })
}

export const deleteWorkout = (id) => {
    return axios.delete(`${prodURL}/workout/${id}`, { withCredentials: true });
}

export const addWorkout = (project) => {
    return axios.post(`${prodURL}/workout`, project, { withCredentials: true });
}

export const exerciseEntry = (exerciseEntry) => {
    return axios.post(`${prodURL}/exerciseentry`, exerciseEntry, { withCredentials: true });
}

